import "./App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import { Row, Col, Container } from "react-bootstrap";

import data from "./data.js";

import axios from "axios";

import { useState } from "react";
import mBanner from "./img/pc_main_02.jpg";

import { Route, Routes } from "react-router-dom";

import Company from "./routes/Company"; /*  */
import Product from "./routes/Product"; /*  */
import Info from "./routes/Info"; /*  */
import Notice from "./routes/Notice"; /*  */

import Footer from "./component/Footer";
import Header from "./component/Header";

import Login from "./pages/Login";
import MyPage from "./pages/MyPage";

import Event from "./routes/Event";
import Detail from "./routes/Detail"; /*  */
import Cart from "./routes/Cart.js";

import SassComponent from "./SassComponent";
import CSSModule from "./CSSModule";
import StyledComponent from "./StyledComponent";

/*   */
import { useNavigate } from "react-router-dom";

/*   */
import ReactPlayer from "react-player";

function App() {
  let [goods, setGoods] = useState(data);
  let [click, setClick] = useState(0);

  const handleClick = () => {
    setClick((click += 1));
    console.log(click);

    axios
      .get(`https://jellyzoo.github.io/reactShop_ex/data${click + 1}.json`)
      .then((res) => {
        console.log(res.data);
        let temp = [...goods, ...res.data];
        setGoods(temp);
      })
      .catch(() => {
        console.log("");
      });
  };

  let navigate = useNavigate();
  function Card(props) {
    function imgClick() {
      navigate("/detail/" + props.i);
    }

    return (
      <Col sm>
        <img src={props.goods.img} alt="cake" width={200} onClick={imgClick} />
        <h4>{props.goods.title}</h4>
        <p>{props.goods.price}</p>
      </Col>
    );
  }

  return (
    <div className="App">
      <Header />

      <Routes>
        <Route
          path="/"
          element={
            <>
              {/*    */}
              {/*       */}
              <div
                className="main-gb"
                style={{
                  backgroundImage: "url(" + mBanner + ")",
                  height: "600px",
                  backgroundPosition: "center",
                }}
              ></div>

              {/*  1 */}
              <Container>
                <Row>
                  {/* map   */}
                  {goods.map((a, i) => {
                    return <Card goods={goods[i]} i={i + 1} key={i} />;
                  })}
                </Row>
                {click < 2 ? (
                  <button
                    onClick={() => {
                      axios
                        .get(
                          "https://runwithoz.github.io/reactShop_ex/data2.json"
                        )
                        .then((result) => {
                          //    
                          let copy = [...goods];
                          copy.push(...result.data);
                          setGoods(copy);
                        })
                        //ajax   
                        .catch(() => {
                          console.log(" .");
                        });
                      handleClick();
                    }}
                  >
                    
                  </button>
                ) : (
                  <div> .</div>
                )}
              </Container>
              {/*  2 */}
              <Container>
                <Row>
                  <Col></Col>
                </Row>
              </Container>

              {/*   pubilc . (   ) */}
              <Container fluid className="contFirst videoWrap">
                <Row>
                  <video controls loop autoPlay muted>
                    <source src="/video/main.mp4"></source>
                  </video>
                </Row>
              </Container>

              {/*  */}
              <Container>
                <Col>
                  <ReactPlayer
                    className="react-player"
                    url={"https://youtu.be/8m5hAJtSdhg?si=5qg-pGw88yZj2730"}
                    width="100%"
                    height="500px"
                    playing={true}
                    muted={true}
                    controls={true}
                    light={false}
                    pip={true}
                    poster={"/video/vidbg.png"}
                  ></ReactPlayer>
                </Col>
              </Container>
            </>
          }
        />
        <Route path="/Company" element={<Company />} />
        <Route path="/Product" element={<Product />} />
        <Route path="/Info" element={<Info />} />
        <Route path="/Notice" element={<Notice />} />

        <Route path="/Login" element={<Login />} />
        <Route path="/MyPage" element={<MyPage />} />
        <Route path="/Cart" element={<Cart />} />
        <Route path="/Event" element={<Event />}>
          <Route
            path="before"
            element={
              <>
                <div></div>
              </>
            }
          />
          <Route
            path="ing"
            element={
              <>
                <div> </div>
              </>
            }
          />
        </Route>

        <Route
          path="*"
          element={<div>    .</div>}
        />
        <Route path="/Detail/:id" element={<Detail goods={goods} />} />

        <Route path="/SassComponent" element={<SassComponent />} />
        <Route path="/CSSModule" element={<CSSModule />} />
        <Route path="/StyledComponent" element={<StyledComponent />} />
      </Routes>

      <Footer />
    </div>
  );
}

export default App;
