import { Link } from "react-router-dom";
const Product = () => {
  return (
    <div>
      <ul>
        <li>
          <Link to="/Company"></Link>
        </li>
        <li>
          <Link to="/Product"></Link>
        </li>
      </ul>
      <p> </p>
    </div>
  );
};

export default Product;
