import { createSlice } from '@reduxjs/toolkit';

//store.jscart .
let cartSlice = createSlice({
  name: 'cart',
  initialState: [],
  reducers: {
    //state   reducer 
    addCount(state, action) {
      //state action  .
      // state[action.payload].count++

      let num = state.findIndex((a) => {
        //a state array~ id   
        return a.id === action.payload; //   
      });
      state[num].count++;
    },
    addItem(state, action) {
     // state.push(action.payload); //push()       
     let num = state.findIndex((obj) =>{
        return obj.id === action.payload.id;
     });
     if(num === -1){
        state.push(action.payload);
     }else{
        state[num].count += action.payload.count;
     }
    },
    delCount(state, action) {
      // state[action.payload].count--
      let num = state.findIndex((a) => {
        //array id   
        return a.id === action.payload; //   
      });
      if (state[num].count > 1) state[num].count--; //1  .
    },
    delItem(state, action) {
      let del = state.filter((a) => {
        return a.id !== action.payload;
      });
      return del;
    },
  },
});

//cart      .
export let { addCount, addItem, delCount, delItem } = cartSlice.actions; //addCount   

export default cartSlice; //store.js   export  .