import "bootstrap/dist/css/bootstrap.min.css";
import { Container, Nav, Navbar } from "react-bootstrap";
import { Link, useNavigate } from "react-router-dom"; //useNavigate    

const Header = () => {
  let navigate = useNavigate(); //     
  return (
    <div>
      <Navbar expand="lg" className="bg-body-tertiary">
        <Container>
          <Link to="/">
            <img src="../img/logo.png" alt="cake"></img>
          </Link>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />

          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link
                onClick={() => {
                  navigate("/Company");
                }}
              >
                
              </Nav.Link>
              <Nav.Link
                onClick={() => {
                  navigate("/Product");
                }}
              >
                
              </Nav.Link>
              <Nav.Link
                onClick={() => {
                  navigate("/Info");
                }}
              >
                
              </Nav.Link>
              <Nav.Link
                onClick={() => {
                  navigate("/Notice");
                }}
              >
                
              </Nav.Link>
              <Nav.Link
                onClick={() => {
                  navigate("/Event");
                }}
              >
                
              </Nav.Link>
              <Nav.Link
                onClick={() => {
                  navigate("/Cart");
                }}
              >
                
              </Nav.Link>
              <Nav.Link
                onClick={() => {
                  navigate(1);
                }}
              >
                
              </Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </div>
  );
};

export default Header;
