import { createSlice } from '@reduxjs/toolkit';

//   export 
let user = createSlice({
    //useState()   .
    name: 'user', //state
    initialState:{name:'shin', age:20 }, // 
  
    //'shin' state   
    reducers: {
      changeName(state) {
        // 
        /* return {name:'', age:21 } */ // state.   
        state.name = '' //state   .
        state.age = 21
      },
      increase(state,action){ //state action .  action .
        state.age += action.payload; //state    . 
        //action.payload       .
      }
    },
  });
  
//user      .
export let { changeName, increase } = user.actions;

export default user; //store.js    .