import Table from 'react-bootstrap/Table';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

import { useSelector, useDispatch } from 'react-redux'; //redux
import { changeName, increase } from './../store/userSlice'; // state 
import { addCount, delCount, delItem } from '../store/cartSlice';
import { Link } from 'react-router-dom';


function Cart() {
  //redux store      
  //   redux store  state   .
  //      
  let state = useSelector((state) => {
    return state;
  });
  let dispatch = useDispatch(); //useDispatch() store   

  // 
  /* let totalPrice = state.cart[0].price * state.cart[0].count;
  let totalPrice2 = state.cart[1].price * state.cart[1].count; */

  let totalPrice = [0]; //
  let i = 0; //   

  for (i = 0; i < state.cart.length; i++) {
    let totalAdd = state.cart[i].price * state.cart[i].count; //      
    totalPrice.push(totalAdd);
  }
  // const sum = totalPrice.reduce((accumulator, currentValue) => accumulator + currentValue);
  const sum = totalPrice.reduce((x, y) => x + y);
  // reduce()     

  /*  (accumulator, currentValue) => accumulator + currentValue,
      (accmulator     +  ) 
 */

  return (
    <Container>

      <Row className="mt-5">
        <Col className="mt-5">
          {state.user.name} {state.user.age} {/*   state */}
          <button onClick={() => { //2.  
            dispatch(increase(10))//    .
          }}>
            
          </button>

          <Table striped bordered hover>
            <thead>
              <tr>
                <th>No</th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {/* map  state     . */}
              {state.cart.map((a, i) => (
                <tr key={i}>
                  <td>{state.cart[i].id}</td>
                  <td><img src={state.cart[i].img} alt="listImg" /></td>
                  <td>{state.cart[i].name}</td>
                  {/* <td>{state.cart[i].count}</td> */}
                  <td>
                    <button
                      onClick={() => {
                        /* state,  id   -1 */
                        dispatch(delCount(state.cart[i].id));
                      }}
                    >
                      -
                    </button>
                    {state.cart[i].count} {/*  */}
                    <button
                      onClick={() => {
                        /* state,  id   +1 */
                        dispatch(addCount(state.cart[i].id));
                      }}
                    >
                      +
                    </button>
                  </td>
                  <td>
                    {state.cart[i].price * state.cart[i].count}
                  </td>
                  <td
                    onClick={() => {
                      dispatch(delItem(state.cart[i].id));
                    }}
                  >
                    
                  </td>
                </tr>
              ))}
              <tr>
                <td colspan="6">
                   : {sum}
                  <Link to="/Login">
                    <button></button>
                  </Link>
                </td>
              </tr>
            </tbody>
          </Table>
        </Col>
      </Row>
    </Container>
  );
}

export default Cart;
